<template>
  <div>
    <div class="search">
      <van-search
        v-model="value"
        placeholder="请输入搜索关键词"
        show-action
        shape="round"
        @search="onSearch"
      >
        <div
          slot="action"
          @click="onSearch"
        >
          搜索
        </div>
      </van-search>
    </div>
    <div>
      <van-pull-refresh
        v-model="isLoading"
        @refresh="onRefresh"
        :head-height="54"
      >
        <p>刷新次数: {{ count }}</p>
      </van-pull-refresh>
    </div>
    <div>
      <GoodsCard
        :goods="goodsList"
      />
    </div>  
  </div>
</template>

<script>
import Vue from 'vue';
import GoodsCard from '../components/GoodsCard'
import axios from 'axios';
import '@vant/touch-emulator';

import { Search, Toast } from 'vant';
import { Button } from 'vant';
import { PullRefresh } from 'vant';

Vue.use(PullRefresh);
Vue.use(Button);
Vue.use(Search);

export default {
    name:'AllItem',
    components: {
        GoodsCard: GoodsCard,
    },
    data() {
        return {
          value: '',
          count: 0,
          isLoading: false,
          search: '',
          goodsList: [{"goodId":4,"userId":"001","goodName":"篮球","number":1,"price":88,"describe":"好！","phone1":null,"phone2":null,"phone3":null},{"goodId":6,"userId":"009","goodName":"足球","number":1,"price":55,"describe":"好！","phone1":null,"phone2":null,"phone3":null},{"goodId":7,"userId":"005","goodName":"电脑","number":1,"price":2000,"describe":null,"phone1":null,"phone2":null,"phone3":null},{"goodId":8,"userId":"008","goodName":"电脑","number":1,"price":3000,"describe":null,"phone1":null,"phone2":null,"phone3":null},{"goodId":4,"userId":"001","goodName":"篮球","number":1,"price":88,"describe":"好！","phone1":null,"phone2":null,"phone3":null},{"goodId":4,"userId":"001","goodName":"篮球","number":1,"price":88,"describe":"好！","phone1":null,"phone2":null,"phone3":null},{"goodId":4,"userId":"001","goodName":"篮球","number":1,"price":88,"describe":"好！","phone1":null,"phone2":null,"phone3":null},{"goodId":4,"userId":"001","goodName":"篮球","number":1,"price":88,"describe":"好！","phone1":null,"phone2":null,"phone3":null}],
        }     
    },
    updated(){
      this.get_allGoods();
    },
    methods: {
      get_allGoods(){
        axios.get('http://localhost:8090/androidApp/Goods/FindAllGoods')
        .then(response=>{
          // eslint-disable-next-line no-console
          console.log(response);
          this.goodsList=response.data;
        })
        .catch(error=>{
          // eslint-disable-next-line no-console
          console.log(error);
        })
      },
      onRefresh() {
        setTimeout(() => {
          this.$toast('刷新成功');
          this.isLoading = false;
          this.count++;
        }, 500);
      },
      onSearch(){
        Toast('搜索');
      },
    },
}
</script>


<style>
  .search{
    position:sticky;
    top:0px;
    z-index: 1;
  }
</style>